body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #f8f9d2;
    background-image: linear-gradient(315deg, #f8f9d2 0%, #e8dbfc 74%);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  flex-direction: column;
}

input {
  padding: 12px;
  margin: 12px;
  font-size: 14px;
  border: 1px solid #ccc;
  box-shadow: 0 1px 4px #ccc;
  border-radius: 6px;
  font-size: 18px;
  width: 90%;
}

button {
  background-color: #fe7bb0;
  background-image: linear-gradient(315deg, #fe7bb0 0%, #ff748b 74%) ;
  border: none;
  color: white;
  padding: 12px;
  margin: 12px 0;
  box-shadow: 0 1px 6px #ccc;
  width: 100%;
  border-radius: 6px;
  font-size: 18px;
}

.container {
  box-shadow: 0 1px 6px #ccc;
  border-radius: 6px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  width: 400px;
  padding: 22px;
}

.container > p {
  cursor: pointer;
  margin: 12px;
}

.form {
  width: 90%;
}

.form span{
 color:  #ff748b ;
 font-weight: 900;

}
.form > h2 {
  text-align: center;
}

.form > form {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.form > form > p {
  cursor: pointer;
}

.error {
  background-color: tomato;
  color: white;
  border-radius: 6px;
  padding: 6px 12px;
  font-weight: bold;
  max-width: 400px;
  margin-bottom: 18px;
}

.newUser{
  color: #ff748b;
  font-weight: 700;
}
.g-btn{
  display: block;
  margin:0px auto;
  height: 40px !important;
  line-height: 40px !important;
  font-size: 15px !important;

}
.g-btn > div,
.g-btn > div > svg {
  width: 40px !important;
  height: 38px !important;
}
.dashboard{
   background-color: #ffcfdf;
   background-image: linear-gradient(315deg, #ffcfdf 0%, #b0f3f1 74%);
   background-repeat: no-repeat;
   width: 100%;
}

.welcome{
   display: flex;
   justify-content: center;
   background-color: #eec0c6; 
   background-image: linear-gradient(315deg, #eec0c6 0%, #7ee8fa 74%);
   margin: 0px;
   padding: 20px;



}
.welcome li{
    list-style: none;
    padding-left: 50px;
    font-size: 20px;
    font-weight: 400;
}

.buttonLi{
  background-color: #fe7bb0;
  background-image: linear-gradient(315deg, #fe7bb0 0%, #ff748b 74%) ;
  border: none;
  color: white;
  padding: 10px;
  margin:-5px 0px 0px 0px;
  box-shadow: 0 1px 6px #ccc;
  width: 100%;
  border-radius: 10px;
  font-size: 15px;

}

.welcome span{
    font-weight: 900;

}

.story{
    width: 80%;
    display: block;
    margin: 30px auto;
}
.asp{
    font-size: 30px;
    text-align: center;
    padding-top: 20px
 }

 .per{
    font-size: 30px;
    text-align: center;

 }

 p{
    font-size: 20px;
    padding-top: 10px;
 }

 svg{
    width: 50px;
    height: 50px;
 }

 .other{
    font-size: 20px;
 }

 @media (max-width: 420px){
   
.welcome{
   display: block;
   background-color: #eec0c6; 
   background-image: linear-gradient(315deg, #eec0c6 0%, #7ee8fa 74%);
   margin: 0px auto;
   padding: 20px;

}
   .welcome li{
      list-style: none;
      padding-top: 10px;
      font-size: 15px;
      font-weight: 400;
  }

  .buttonLi{
   background-color: #fe7bb0;
   background-image: linear-gradient(315deg, #fe7bb0 0%, #ff748b 74%) ;
   border: none;
   color: white;
   padding: 10px;
   margin: 20px 0px 0px 0px;
   box-shadow: 0 1px 6px #ccc;
   width: 50%;
   border-radius: 10px;
   font-size: 15px;
 
 }

 }
